import React from 'react'
import PropTypes from 'prop-types';
import Picto from './Picto'

/**
 *
 * @param {object} param
 * @param {string} param.value the value of the element
 * @param {string} param.title the title of the element
 * @param {color} param.color the color of the image
 * @returns <Info value={value} title={title} color={color}/>
 */
/**
 * Afficher un block d'information
 *
 * @component
 * @example
 * const value = '580 g'
 * const title = 'Lipides'
 * const color = '#FD5181'
 * return (
 *   <Info value={value} title={title} color={color} />
 * )
 */
export default function Info ({value,title,color}){

    return <div className="info">
        <Picto type={title} color={color}/>
        <div className="info__content">
            <div className="info__value">{value}</div>
            <div className="info__title">{title.toUpperCase()}</div>
        </div>
    </div>
}

Info.propTypes = {
    value: PropTypes.string,
    title: PropTypes.string,
    color: PropTypes.string,
};
