import PropTypes from 'prop-types';
import React,{ useEffect, useRef, useState } from "react";
import { RadialBarChart, RadialBar, Legend } from "recharts";

export default function Score({user}) {
    const [width, setWidth] = useState(null)
    const graphRef = useRef(null);

    /**
     * Set the with of this component
     */
    useEffect(() => {
        const { offsetWidth } = graphRef.current
        if (width !== offsetWidth) {
            setWidth(offsetWidth)
        }
    }, [width])

    /**
     * the data of this component
     */
    const data = [
        {
            value: user.score,
            fill: "#FF0000"
        }
    ];

    /**
     * Custom style of <Legend/>
     */
    const style = {
        box: {
            top: 0,
            left: 0,
            right: 0,
            bottom: 0,
            display: 'flex',
            justifyContent: 'center',
            alignItems: 'center',
            lineHeight: "24px",
            width: '100%',
            height: '100%',
            color: 'grey',
        },
    };

    /**
     * The custom legend of this graph
     * @param {object} props
     * @returns <RenderLegend/>
     */
    const RenderLegend = (props) => {
        const { payload } = props;
        return (
            <div className="scoreLegend">
                <div className="scoreLegend__title">Score</div>
                <div className="scoreLegend__info"><span className="scoreLegend__value">{payload[0].payload.value * 100}%</span>
                <br/>de votre objectif</div>
            </div>
        );
    }

    return (
        <div className="score"
            ref={graphRef}
            >
                <RadialBarChart
                    width={width}
                    height={width}
                    innerRadius={width/2}
                    barSize={10}
                    data={data}
                    startAngle={90}
                    endAngle={(data[0].value * 360 + 90)}
                >
                    <RadialBar
                        minAngle={15}
                        cornerRadius={50}
                        dataKey="value"
                    />
                    <Legend
                        iconSize={10}
                        width={120}
                        height={140}
                        layout="vertical"
                        verticalAlign="middle"
                        content={<RenderLegend/>}
                        wrapperStyle={style.box}
                    />
                </RadialBarChart>
        </div>
    );
}

Score.prototype={
    user:PropTypes.shape({
        score:PropTypes.number.isRequired
    })
}
