import PropTypes from 'prop-types';
import React,{ useEffect, useRef, useState } from "react";
import Spinner from './Spinner'
import {
    BarChart,
    Bar,
    XAxis,
    YAxis,
    CartesianGrid,
    Tooltip,
    Legend
} from "recharts";



/**
 * Use Activity component for create the graph withe
 * @param {object} props
 * @param {object} props.user the current user object
 * @return <Activity user={user}/>
 */

export default function Activity({user}) {

    const [width, setWidth] = useState(null)
    const graphRef = useRef(null);
    const [data, setData] = useState(null)

    /**
     * Set the data graph
     */
    useEffect(()=>{
        user.loadActivity()
            .then((user) => setData(user.activity))
    },[user])

    /**
     * Set the with of this component
     */
    useEffect(() => {
        const { offsetWidth } = graphRef.current
        if (width !== offsetWidth){
            setWidth(offsetWidth)
        }
    }, [width])

    /**
     * Function use by <Legend/>
     * @returns The graph legend
     */
    const renderLegend = () => {
        return (
            <div className="legendActivity">
                <span className="legendActivity__title">Activité quotidienne</span>
                <span className="legendActivity__values">
                    <span className="legendActivity__poids">
                        <svg className="legendActivity__icon" width="8" height="8" viewBox="0 0 8 8" fill="none" xmlns="http://www.w3.org/2000/svg">
                            <path fillRule="evenodd" clipRule="evenodd" d="M4 8C6.20914 8 8 6.20914 8 4C8 1.79086 6.20914 0 4 0C1.79086 0 0 1.79086 0 4C0 6.20914 1.79086 8 4 8Z" fill="#282D30" />
                        </svg>
                        Poids (kg)
                    </span>
                    <span className="legendActivity__cal">
                        <svg className="legendActivity__icon" width="8" height="8" viewBox="0 0 8 8" fill="none" xmlns="http://www.w3.org/2000/svg">
                            <path fillRule="evenodd" clipRule="evenodd" d="M4 8C6.20914 8 8 6.20914 8 4C8 1.79086 6.20914 0 4 0C1.79086 0 0 1.79086 0 4C0 6.20914 1.79086 8 4 8Z" fill="#E60000" />
                        </svg>
                        Calories brûlées (kCal)
                    </span>
                </span>
            </div>
        );
    }

    /**
     * Create the custom Bar
     * @param {object} props Send by <Bar/>
     * @returns the custom bar
     */
    const RenderBar = (props) => {
        const { fill, x, y, width, height } = props;
        return <g fill={fill}>
            <rect y={y} x={x} width={width} height={height} />
            <circle cx={x + width / 2} cy={y} r={width / 2} />
        </g>
    }
    RenderBar.propTypes = {
        fill: PropTypes.string,
        x: PropTypes.number,
        y: PropTypes.number,
        width: PropTypes.number,
        height: PropTypes.number,
    };

    /**
     * Create a custom tooltip
     * @param {object} param
     * @param {boolean} param.active send by <Tooltip/>
     * @param {object} param.payload send by <Tooltip/>
     * @returns The new tooltip or null if not active
     */
    const RenderTooltip = ({ active, payload }) => {
        if (active && payload && payload.length) {
            return (
                <div className="tooltip">
                    {payload.map((item, i) => <div key={i} className="tooltip__text">{item.value} {item.unit}</div>)}
                </div>
            );
        }
        return null;
    };
    RenderTooltip.propTypes = {
        active: PropTypes.bool,
        payload: PropTypes.arrayOf(
            PropTypes.shape({
                value:PropTypes.number,
                unit: PropTypes.string,
            })
        ),
    };



    return (
        <div
            ref={graphRef}
            className="activity"
        >
            {!data?<Spinner/>:
                <BarChart
                    width={width}
                    height={width*38/100}
                    data={data}
                    margin={{
                        top: 5,
                        right: 30,
                        left: 20,
                        bottom: 5
                    }}
                >
                    <CartesianGrid
                        strokeDasharray="2 2"
                        vertical={false}
                    />
                    <XAxis
                        dataKey="name"
                        axisLine={true}
                        tickLine={false}
                    />
                    <YAxis
                        axisLine={false}
                        tickLine={false}
                        orientation={'right'}
                    />
                    <Tooltip
                        content={<RenderTooltip />}
                    />
                    <Legend
                        content={renderLegend}
                        verticalAlign="top"
                        align="right"
                        height={80}
                    />
                    <Bar
                        dataKey="kilogram"
                        fill="#282D30"
                        shape={<RenderBar />}
                        barSize={7}
                        unit={'kg'}
                    />
                    <Bar
                        dataKey="calories"
                        fill="#E60000"
                        shape={<RenderBar />}
                        barSize={7}
                        unit={'Kcal'}
                    />
                </BarChart>
            }
        </div>
    );
}

Activity.propTypes = {
    user: PropTypes.shape({
        id: PropTypes.number.isRequired,
        loadActivity: PropTypes.func.isRequired,
    })
};
