import PropTypes from 'prop-types';
import Spinner from './Spinner'
import React,{ useEffect, useRef, useState } from "react";
import {
    Radar,
    RadarChart,
    PolarGrid,
    PolarAngleAxis,
} from "recharts";

/**
 *
 * @param {object} param
 * @param {object} props.user the current user object
 * @returns <Intensityuser={user}/>
 */
export default function Intensity({user}) {
    const [width, setWidth] = useState(null)
    const graphRef = useRef(null);
    const [data, setData] = useState(null)

    /**
     * set the data's graph
     */
    useEffect(() => {
        user.loadIntensity()
            .then((user) => setData(user.intensity))
    }, [user])

    /**
     * Set the with of this component
     */
    useEffect(() => {
        const { offsetWidth } = graphRef.current
        if (width !== offsetWidth) {
            setWidth(offsetWidth)
        }
    }, [width])

    return (
        <div className="intensity"
            ref={graphRef}>
            {!data ? <Spinner /> :
            <RadarChart
                cx={width/2}
                cy={width/2}
                width={width}
                height={width}
                data={data}
                margin={
                    { top: 50, right: 50, bottom: 50, left: 50 }
                }
            >
                <PolarAngleAxis
                    dataKey="label"
                    tickFormatter={x=>{
                        const element = data.find(elt=>elt.label===x)
                        return element.labelfr
                    }}
                />
                <PolarGrid />
                <Radar
                    dataKey="value"
                    fill="#FF0101"
                    fillOpacity={0.6}
                    label={false}
                />
            </RadarChart>
            }
        </div>
    );
}

Intensity.propTypes = {
    user: PropTypes.shape({
        id: PropTypes.number.isRequired,
        loadIntensity: PropTypes.func.isRequired,
    })
};
